.question-tag {
  padding: 10px 20px;
  border: 2px solid #000;
  list-style-type: none;
  cursor: pointer;
}

.question-slider {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-block: 2rem;
}

.active-ques {
  background: #000;
  color: #fff;
}

.hr {
  border: 1px solid gainsboro;
  margin-block: 15px;
}

.question-cont {
  max-width: 350px;
  margin-inline: auto;
  margin-block: 150px;
  border: 2px solid black;
  min-height: 350px;
  padding: 30px;
}

.question {
  font-size: 20px;
  text-align: center;
  margin-block: 30px;
}

.answer {
  font-size: 22px;
  text-align: center;
  color: green;
}

.each-response {
}

.activity-cont {
  margin-inline: auto;
  text-align: center;
  padding-inline: 100px;
  margin-bottom: 80px;
  max-width: 800px;
}

.res-head {
  text-align: center;
  font-size: 20px;
  margin-bottom: 50px;
}

.ques {
  flex-grow: 1;
  text-align: start;
}

@media (max-width: 500px) {
  .activity-cont {
    padding-inline: 10px;
  }
}

td {
  border: 1px solid #000;
  padding: 10px 5px;
  width: 200px;
  text-align: center;
}
