.login-master-btn {
  background-image: linear-gradient(
    to right,
    #e55d87 0%,
    #5fc3e4 51%,
    #e55d87 100%
  );
  margin: 10px;
  padding: 15px 45px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 20px #eee;
  border-radius: 10px;
  display: block;
}

.login-master-btn:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}

.login-input-field {
  border: 1px solid #e55d87;
  border-radius: 5px;
  height: 30px;
  width: 200px;
  font-size: 18px;
  padding-left: 10px;
  margin-top: 15px;
}

.login-username-sec,
.login-password-sec {
  margin-block: 30px;
}

.login-form-cont {
  border-left: 5px solid #e55d87;
  padding-left: 30px;
}

.login-btn {
  padding: 10px 40px;
  border: 2px solid #e55d87;
  background: transparent;
  transition: all 2ms;
  border-radius: 10px;
}

.login-btn:hover {
  border: 2px solid transparent;
  background: #5d9fe5;
}

@media (max-width: 500px) {
  .login-form-cont {
    margin-top: 50px;
    padding-top: 20px;
    border: none;
    border-top: 5px solid #e55d87;
  }
}
