.question-tag {
  padding: 10px 20px;
  border: 2px solid #000;
  list-style-type: none;
  cursor: pointer;
}

.question-slider {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-block: 2rem;
}

.active-ques {
  background: #000;
  color: #fff;
}

.hr {
  border: 1px solid gainsboro;
  margin-block: 15px;
}

.question-cont {
  max-width: 350px;
  margin-inline: auto;
  margin-block: 150px;
  border: 2px solid black;
  min-height: 350px;
  padding: 30px;
}

.question {
  font-size: 20px;
  text-align: center;
  margin-block: 30px;
}

.answer {
  font-size: 22px;
  text-align: center;
  color: green;
}

.submitTestBtn {
  color: #000;
  padding: 15px;
  border: 2px solid black;
}

.ans-input {
  border: 1px solid black;
  padding-left: 10px;
  padding-block: 5px;
  display: block;
  width: 100%;
  margin-top: 15px;
}

.sub-btn {
  margin-top: 20px;
  display: flex;
  justify-content: space-evenly;
}
